'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/app/AuthContext';
import { User, UserRole, UserStatus } from '@/types';

export default function Auth() {
  const [isLogin, setIsLogin] = useState(true);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [name, setName] = useState('');
  
  const router = useRouter();
  const { handleLogin } = useAuth();

  const handleSubmit = () => {
    if (isLogin) {
      if (!email || !password) {
        alert('Please fill in all fields');
        return;
      }
      
      const mockUser: User = {
        id: '1',
        firstName: name.split(' ')[0] || 'Admin',
        lastName: name.split(' ')[1] || 'User',
        studentId: 'MASTER-001',
        email: email,
        phone: '+1 800 LAB ADMIN',
        registrationDate: new Date().toISOString(),
        effectiveFrom: new Date().toISOString(),
        effectiveTo: '2030-01-01',
        status: UserStatus.ACTIVE,
        role: UserRole.MASTER
      };
      
      handleLogin(mockUser);
      setTimeout(() => {
        router.push('/dashboard');
      }, 100);
    } else {
      if (password !== confirmPassword) {
        alert('Passwords do not match');
        return;
      }
      if (!name || !email || !password) {
        alert('Please fill in all fields');
        return;
      }
      
      const newUser: User = {
        id: Date.now().toString(),
        firstName: name.split(' ')[0],
        lastName: name.split(' ')[1] || '',
        studentId: 'USER-' + Date.now(),
        email: email,
        phone: '',
        registrationDate: new Date().toISOString(),
        effectiveFrom: new Date().toISOString(),
        effectiveTo: '2030-01-01',
        status: UserStatus.ACTIVE,
        role: UserRole.USER
      };
      
      handleLogin(newUser);
      setTimeout(() => {
        router.push('/dashboard');
      }, 100);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-blue-950 flex items-center justify-center p-6 relative overflow-hidden">
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute w-96 h-96 bg-blue-500 rounded-full opacity-10 blur-3xl -top-48 -left-48 animate-pulse"></div>
        <div className="absolute w-96 h-96 bg-indigo-500 rounded-full opacity-10 blur-3xl -bottom-48 -right-48 animate-pulse"></div>
      </div>

      <div className="w-full max-w-6xl grid md:grid-cols-2 gap-0 backdrop-blur-xl bg-black/30 rounded-3xl shadow-2xl overflow-hidden border border-white/10 relative z-10">
        
        {/* Left Side - Glassmorphic Form */}
        <div className="p-10 lg:p-12 backdrop-blur-2xl bg-gradient-to-br from-black/60 via-black/50 to-black/40 relative">
          {/* Glass effect overlay */}
          <div className="absolute inset-0 bg-gradient-to-br from-white/5 to-transparent pointer-events-none"></div>
          
          <div className="relative z-10">
            <div className="mb-8">
              <div className="flex items-center mb-6">
                <img src="/sm.png" alt="SmartLab" className="w-12 h-12 rounded-xl shadow-lg shadow-blue-500/50 mr-3" />
                <div>
                  <span className="text-2xl font-bold bg-gradient-to-r from-white to-blue-200 bg-clip-text text-transparent">SmartLab</span>
                </div>
              </div>
              <h1 className="text-4xl font-bold text-white mb-3">
                {isLogin ? 'Welcome Back' : 'Get Started'}
              </h1>
              <p className="text-slate-300">
                {isLogin ? 'Sign in to access your lab dashboard' : 'Create your account to begin'}
              </p>
            </div>

            <div className="space-y-4">
              {!isLogin && (
                <div>
                  <label className="block text-sm font-semibold text-slate-200 mb-2">
                    Full Name
                  </label>
                  <input
                    type="text"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="w-full px-4 py-3 border border-white/20 rounded-xl focus:border-blue-500 focus:outline-none transition-all bg-white/5 backdrop-blur-xl text-white placeholder-slate-400 hover:bg-white/10"
                    placeholder="Dr. John Smith"
                  />
                </div>
              )}

              <div>
                <label className="block text-sm font-semibold text-slate-200 mb-2">
                  Email Address
                </label>
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="w-full px-4 py-3 border border-white/20 rounded-xl focus:border-blue-500 focus:outline-none transition-all bg-white/5 backdrop-blur-xl text-white placeholder-slate-400 hover:bg-white/10"
                  placeholder="lab.admin@smartlabe.com"
                />
              </div>

              <div>
                <label className="block text-sm font-semibold text-slate-200 mb-2">
                  Password
                </label>
                <input
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full px-4 py-3 border border-white/20 rounded-xl focus:border-blue-500 focus:outline-none transition-all bg-white/5 backdrop-blur-xl text-white placeholder-slate-400 hover:bg-white/10"
                  placeholder="••••••••"
                />
              </div>

              {!isLogin && (
                <div>
                  <label className="block text-sm font-semibold text-slate-200 mb-2">
                    Confirm Password
                  </label>
                  <input
                    type="password"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    className="w-full px-4 py-3 border border-white/20 rounded-xl focus:border-blue-500 focus:outline-none transition-all bg-white/5 backdrop-blur-xl text-white placeholder-slate-400 hover:bg-white/10"
                    placeholder="••••••••"
                  />
                </div>
              )}

              {isLogin && (
                <div className="flex items-center justify-between text-sm">
                  <label className="flex items-center text-slate-300 cursor-pointer">
                    <input type="checkbox" className="mr-2 rounded w-4 h-4 bg-white/10 border-white/20" />
                    <span>Remember me</span>
                  </label>
                  <button className="text-blue-400 hover:text-blue-300 font-semibold">
                    Forgot password?
                  </button>
                </div>
              )}

              <button
                onClick={handleSubmit}
                className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 text-white py-3.5 rounded-xl font-semibold hover:from-blue-500 hover:to-indigo-500 transition-all shadow-lg shadow-blue-500/30 hover:shadow-blue-500/50 transform hover:-translate-y-0.5 mt-6 border border-blue-400/20"
              >
                {isLogin ? 'Sign In to Dashboard' : 'Create Account'}
              </button>
            </div>

            <div className="mt-6 text-center">
              <p className="text-slate-400 text-sm">
                {isLogin ? "Don't have an account? " : 'Already have an account? '}
                <button
                  onClick={() => setIsLogin(!isLogin)}
                  className="text-blue-400 hover:text-blue-300 font-bold"
                >
                  {isLogin ? 'Sign Up' : 'Sign In'}
                </button>
              </p>
            </div>

            <div className="mt-8 mb-8 flex items-center">
              <div className="flex-1 border-t border-white/10"></div>
              <span className="px-4 text-slate-400 text-sm font-medium">OR</span>
              <div className="flex-1 border-t border-white/10"></div>
            </div>

            <button className="w-full flex items-center justify-center px-4 py-3 border border-white/20 rounded-xl backdrop-blur-xl bg-white/5 hover:bg-white/10 transition-all">
              <svg className="w-5 h-5 mr-2" viewBox="0 0 24 24">
                <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
                <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
                <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
                <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
              </svg>
              <span className="text-slate-200 text-sm font-semibold">Continue with Google</span>
            </button>

            <div className="mt-8 pt-6 border-t border-white/10">
              <p className="text-xs text-slate-500 text-center">
                By signing in, you agree to our Terms of Service and Privacy Policy
              </p>
            </div>
          </div>
        </div>

        {/* Right Side - Lab Management Info Panel */}
        <div className="bg-gradient-to-br from-blue-600 via-blue-700 to-indigo-800 p-10 lg:p-12 flex flex-col justify-center text-white relative overflow-hidden">
          <div className="absolute top-0 right-0 w-64 h-64 bg-white opacity-5 rounded-full -mr-32 -mt-32"></div>
          <div className="absolute bottom-0 left-0 w-96 h-96 bg-white opacity-5 rounded-full -ml-48 -mb-48"></div>
          
          <div className="relative z-10">
            <div className="mb-8">
              <div className="w-16 h-16 bg-white bg-opacity-20 rounded-2xl flex items-center justify-center mb-6 backdrop-blur-sm">
                <svg className="w-10 h-10 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 3v2m6-2v2M9 19v2m6-2v2M5 9H3m2 6H3m18-6h-2m2 6h-2M7 19h10a2 2 0 002-2V7a2 2 0 00-2-2H7a2 2 0 00-2 2v10a2 2 0 002 2zM9 9h6v6H9V9z" />
                </svg>
              </div>
              <h2 className="text-4xl font-bold mb-4 leading-tight">
                Next-Gen Laboratory Management
              </h2>
              <p className="text-blue-100 text-lg leading-relaxed">
                SmartLab brings AI-powered intelligence to your laboratory operations. Streamline workflows, track experiments, and collaborate seamlessly.
              </p>
            </div>

            <div className="space-y-6 mt-12">
              <div className="flex items-start space-x-4">
                <div className="w-10 h-10 bg-white bg-opacity-20 rounded-lg flex items-center justify-center flex-shrink-0 backdrop-blur-sm">
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-6 9l2 2 4-4" />
                  </svg>
                </div>
                <div>
                  <h3 className="font-semibold text-lg mb-1">Smart Sample Tracking</h3>
                  <p className="text-blue-100 text-sm">AI-powered sample management with automated chain of custody</p>
                </div>
              </div>

              <div className="flex items-start space-x-4">
                <div className="w-10 h-10 bg-white bg-opacity-20 rounded-lg flex items-center justify-center flex-shrink-0 backdrop-blur-sm">
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4" />
                  </svg>
                </div>
                <div>
                  <h3 className="font-semibold text-lg mb-1">Equipment Intelligence</h3>
                  <p className="text-blue-100 text-sm">Predictive maintenance and real-time equipment monitoring</p>
                </div>
              </div>

              <div className="flex items-start space-x-4">
                <div className="w-10 h-10 bg-white bg-opacity-20 rounded-lg flex items-center justify-center flex-shrink-0 backdrop-blur-sm">
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                  </svg>
                </div>
                <div>
                  <h3 className="font-semibold text-lg mb-1">Data Analytics</h3>
                  <p className="text-blue-100 text-sm">Advanced insights and automated compliance reporting</p>
                </div>
              </div>
            </div>

            <div className="mt-12 pt-8 border-t border-white border-opacity-20">
              <p className="text-sm text-blue-100 italic">
                "SmartLab transformed our research efficiency. The intelligent automation is a game-changer."
              </p>
              <p className="text-sm font-semibold mt-3">— Dr. Sarah Martinez, Research Director</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}